import React from "react";
import "../css/navbar.css";
import { NavLink } from "react-router-dom";

const Navbar = () => {
  return (
    <nav>
      <ul>
        <li><NavLink to="/Homepage"><i class="fa-solid fa-house fa-xs"></i> Home Page</NavLink></li>
        <li><NavLink to="/Login"><i class="fa-solid fa-arrow-right-to-bracket fa-sm"></i> Login</NavLink></li>
        <li><NavLink to="/Signup"><i class="fa-solid fa-right-to-bracket fa-sm"></i> Signup</NavLink></li>
        <li><NavLink to="/Calculator"><i class="fa-solid fa-calculator fa-sm"></i> Calculator</NavLink></li>
        <li><NavLink to="/Employlist"><i class="fa-solid fa-list fa-sm"></i> Employlist</NavLink></li>
        {/* <li><NavLink to="/EmployeeDetails"> Employee Details</NavLink></li> */}
        <li><NavLink to="/Pagination">Pagination</NavLink></li>
      </ul>
    </nav>
  );
};

export default Navbar;