import React from "react";
import "../css/table.css";

const Table = (props) => {
  const [currentPage, setcurrentPage] = React.useState(1);
  const [pageLimit, setpageLimit] = React.useState(5);
  const [lastPage, setlastPage] = React.useState(1);
  console.log(props.data);

  const generateHead = (singleObject) => {
    let keys = Object.keys(singleObject);
    return keys.map((each) => <th>{each}</th>);
  };

  const generateData = (singleObject) => {
    let keys = Object.keys(singleObject);
    return keys.map((each) => <td>{singleObject[each]}</td>);
  };

  const generateBody = (tableData, page) => {

    let start = (page - 1) * pageLimit;
    let end = start + pageLimit;

    let filterData = tableData.slice(start, end);
    return filterData.map((each) => <tr>{generateData(each)}</tr>);
  };

  const displayPage = (page) => {
    return generateBody(props.data, page)
  }

  const generateButtons = () => {
    let start = currentPage-2 <1?1: currentPage-2;
    let end = lastPage+2<lastPage? currentPage+2 : lastPage;
    let buttons = [];
    for (let i = start; i <= end; i++) {
      buttons.push(i)
    }
    return buttons.map((each) =>
      <button className={currentPage === each ? 'active' : ''} onClick={() => setcurrentPage(each)}>{each}</button>
    );
  }

  React.useEffect(() => {
    let mylastPage = Math.ceil(props.data.length / pageLimit);
    setlastPage(mylastPage)
    setcurrentPage(1)
  }, [pageLimit])
  return (
    <div>
      <div class="buttons">
        <button onClick={() => setcurrentPage(1)}>&lt;&lt;</button>
        <button disabled ={currentPage===1}onClick={() => setcurrentPage(currentPage - 1)}>&lt;</button>
        {/* <button onClick={() => setcurrentPage(1)}>1</button>
          <button onClick={() => setcurrentPage(2)}>2</button>
          <button onClick={() => setcurrentPage(3)}>3</button>
          <button onClick={() => setcurrentPage(4)}>4</button>
          <button onClick={() => setcurrentPage(5)}>5</button> */}
        {generateButtons()}
        <button disabled ={currentPage===lastPage}onClick={() => setcurrentPage(currentPage + 1)}>&gt;</button>
        <button onClick={() => setcurrentPage(lastPage)}>&gt;&gt;</button>
        <select name="" id="" onChange={(e) => setpageLimit(parseInt(e.target.value))}>
          <option value={5}>5</option>
          <option value={10}>10</option>
          <option value={15}>15</option>
          <option value={20}>20</option>
        </select>
      </div>

      <div>
        <table className="pag__table">
          <thead>
            <tr id="heading">{generateHead(props.data[0])}</tr>
          </thead>
          <tbody id="data">
            {displayPage(currentPage)}
          </tbody>
        </table>
      </div>

    </div>
  );
};

export default Table;
