import React from "react";
import {useForm} from "react-hook-form";
import "../css/signup.css";


const Signup = () => {
    const {register,handleSubmit, formState:{errors}} = useForm();

    const onSubmit = (data) =>{
        console.log(data);
    }
    return (

    <div class="signup-container">
        <h2>Sign Up</h2>
        <form onSubmit={handleSubmit(onSubmit)}>
            <input type="text" name="fullname" placeholder="Full Name" {...register('fullname')}/>
            <input type="text" name="username" placeholder="Username" {...register('username',{
                required:{
                    value:true,
                    message:'usename is required'
                },
                minLength: {
                    value:3,
                    message:'username should have aleast 3 characters'
                },
                maxLength: {
                    value:15,
                    message:'username should have at 15 characters'
                }
            })}/>
            <small>{errors.username?.message}</small>
            <input type="email" name="email" placeholder="Email" {...register('email')}/>
            <input type="password" name="password" placeholder="Password" {...register('password' ,{
                required:{
                    value:/^(?=.*\d)(?=.*[a-zA-Z])[a-zA-Z0-9]{7,}$/,
                    message:'password must be numeric, uppercase, smallercase' 
                }
            })}/>
            <small>{errors.password?.message}</small>
            <button type="submit">Sign Up</button>
        </form>
    </div>
     
    )
}

export default Signup;