import React from "react";
import "../css/login.css";
import {useForm} from "react-hook-form";

const Login = () => {
    const { register, handleSubmit ,formState:{errors}} = useForm();

    const onSubmit = (data) =>{
        console.log(data);
    }
    return (

        <div class="login-container">
            <h2>Login</h2>
            <form onSubmit={handleSubmit(onSubmit)}>
                <input type="text" name="username" placeholder="Username" {...register('username',{
                    required:{
                    value:true,
                    message:'username is required'
                },
                minLength: {
                    value:3,
                    message:'username should have aleast 3 characters'
                },
                maxLength: {
                    value:15,
                    message:'username should have at 15 characters'
                }
                })} />
                <small>{errors.username?.message}</small>
                <input type="password" name="password" placeholder="Password" {...register('password',{
                required:{
                    value:/^(?=.*\d)(?=.*[a-zA-Z])[a-zA-Z0-9]{7,}$/,
                    message:'password must be numeric, uppercase, smallercase'
                }
                })} />
                <small>{errors.password?.message}</small>
                <button type="submit">Log In</button>
            </form>
        </div>

    )
}

export default Login;