import React from "react";
import profile from "../assets/Employee Profile.png";
import "../css/card.css";

const Card = (props) => {
    return (
        <div className="container">
            <h1>Employee Profile</h1>
            <div className="profile">
                <img src={profile} alt="Employee Profile" />
                <h2>Name:{props.emp.Name}</h2>
                <h3>ID No:{props.emp.EmployeeID}</h3>
                <button onClick={() => props.btnfn()} id="view-btn">{props.btntext}</button>
                {/* <button onClick={() => props.btnfn()} id="back-btn">{props.btntext}</button> */}
            </div>
        </div>
    )
}

export default Card;